// @mui
import { useEffect, useState } from 'react';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import DataTable from 'react-data-table-component';
import { InputAdornment, TextField, Button, Box, Tabs, Tab, Stack, Chip, Card, CardContent, Modal, Grid, FormControl, InputLabel, Select, MenuItem, Autocomplete, OutlinedInput, Checkbox, ListItemText } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import AddIcon from '@mui/icons-material/Add';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import { color, common, decryptToken, formatObj } from 'src/utils/common';
import CashImage from 'src/assets/images/universal_currency_alt.png';
import CreditCardImage from 'src/assets/images/credit-card 1.png';
import UPIImage from 'src/assets/images/operation 1.png';
import CheckBook from 'src/assets/images/cheque-book.png';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';

// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 700,
  bgcolor: color.modalbackground,
  boxShadow: 24,
  borderRadius: '8px',
  p: 2,
  height: 'auto'
};



const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 650,
    },
  },
};


const categories = [
  'ELECTRICITY BILL',
  'REPAIR AND MAINTAINANCE',
  'INTERNET BILLS',
  'RAW MATERIAL',
  'RENT',
  'PRINTING',
  'EMPLOYEE SALARIES',
];
export default function PaymentsView() {
  const [invoicesList, setInvoicesList] = useState([]);
  const [filteredInvoicesList, setFilteredInvoicesList] = useState([]);

  const handleChangeSelect = (event) => {
    const {
      target: { value },
    } = event;

    if (value.includes('select-all')) {
      // Ignore the event when "Select All" is clicked
      return;
    }

    setPaymentData((prev) => ({ ...prev, invoices: typeof value === 'string' ? value.split(',') : value }))
  };

  const handleSelectAll = (event) => {
    if (paymentData.invoices.length === filteredInvoicesList.length) {
      setPaymentData((prev) => ({ ...prev, invoices: [] }))
    } else {
      setPaymentData((prev) => ({ ...prev, invoices: filteredInvoicesList.map(invoiceObject => invoiceObject.invoiceNumber) }))
    };
  };

  const settings = useSettingsContext();
  const { enqueueSnackbar } = useSnackbar();
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [type, setType] = useState(['VENDOR']);
  const [banks, setBanks] = useState([]);

  const [paymentData, setPaymentData] = useState({
    partyType: 'VENDOR',
    vendorId: '',
    category: '',
    invoices: [],
    invoiceAmount: 0,
    discount: 0,
    totalAmount: 0,
    paymentDate: new Date(),
    paymentMode: '',
    chequeNumber: '',
    bank: ''
  })
  const [data, setData] = useState({
    balanceSummary: {},
    typeSummaryDifferedByPaymentModeTypes: [],
    payments: []
  });
  const [vendors, setVendors] = useState([]);


  const filteredRecords = data.payments.filter((record) => record?.invoiceNumber?.toLowerCase().includes(searchQuery?.toLowerCase())
  );

  const fetchData = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.expenditure.paymentSummary, { type }, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setIsLoading(false);
        setData(response.data.data);
        console.log(response.data.data);
      };

    } catch (err) {
      enqueueSnackbar(err.error || 'Failed to fetch records!', { variant: 'error' });
      setIsLoading(false);
    }
  };
  const fetchVendors = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.vendor.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setVendors(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch vendors!', { variant: 'error' });
    }
  };
  const fetchBanks = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.bank.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setBanks(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch bank accounts!', { variant: 'error' });
    }
  };

  useEffect(() => {
    fetchVendors();
    fetchBanks();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    setIsLoading(true);
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [type]);

  // For Getting invoices list
  useEffect(() => {
    //  Call the api based on partyType if partyType is vendor then call purchase orders and if expense then call expense list 
    const endpointURL = paymentData.partyType === 'EXPENSE' ? endpoints.expenditure.list : endpoints.purchase.list;
    const fetchRecords = async () => {
      try {
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        const response = await axios.post(endpointURL, {}, {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          }
        });
        if (response.data.data) {
          setIsLoading(false);
          let remainingAmountData = response.data.data.filter((record) => record.remainingAmount > 0);
          if (paymentData.partyType === 'EXPENSE') {
            remainingAmountData = remainingAmountData.filter((record) => record.type === 'EXPENSE');
          };
          console.log(remainingAmountData)
          setInvoicesList(remainingAmountData);
          setFilteredInvoicesList(remainingAmountData);
        };
      } catch (err) {
        enqueueSnackbar('Failed to fetch records!', { variant: 'error' });
        setIsLoading(false);
      }
    };
    fetchRecords();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [paymentData.partyType])

  const columns = [
    {
      name: 'Invoice Number',
      selector: (row) => row.invoiceNumber,
      sortable: true,
    },
    {
      name: 'Amount Paid',
      selector: (row) => row.amount,
      sortable: true,
    },
    {
      name: 'Payment Mode',
      selector: (row) => row.paymentMode,
      sortable: true,
    },
    {
      name: 'Payment Status',
      selector: (row) => row.paymentStatus,
      sortable: true,
    },
    {
      name: 'Party Type',
      selector: (row) => row.partyType,
      sortable: true,
    },
    {
      name: 'Payment Date',
      selector: (row) => new Date(row.createdAt).toLocaleDateString('en-GB', { day: '2-digit', month: 'long', year: 'numeric' }),
      sortable: true,
    },
    {
      name: 'Actions',

    },
  ];
  const [value, setValue] = useState('one');

  const handleChange = (event, newValue) => {
    setValue(newValue);
    if (newValue === 'one') {
      setType(['VENDOR']);
    } else {
      setType(['INCOME', 'EXPENSE']);
    }
  };

  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const handleAddPayment = async () => {

    const paymentPayload = {
      partyType: paymentData.partyType,
      invoices: paymentData.invoices,
      totalAmount,
      paymentDate: dayjs(paymentData.paymentDate).format('YYYY-MM-DD'),
      paymentMode: paymentData.paymentMode,
      bankId: paymentData.bank,
      discount: parseFloat(paymentData.discount),
      chequeNumber: paymentData.chequeNumber
    };
    console.log({ paymentPayload });

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.payment.add, paymentPayload, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        enqueueSnackbar('Payment added successfully!', { variant: 'success' });
        setOpen(false);
        setPaymentData({
          partyType: 'VENDOR',
          vendorId: '',
          category: '',
          invoices: [],
          invoiceAmount: 0,
          discount: 0,
          totalAmount: 0,
          paymentDate: new Date(),
          paymentMode: '',
          chequeNumber: '',
          bank: ''
        })
        fetchData();
      }
    } catch (err) {
      console.log(err);
      enqueueSnackbar('Failed to add payment!', { variant: 'error' });
    }
  };

  let invoiceAmount = 0;
  let totalAmount = 0;
  if (paymentData.invoices.length > 0) {
    invoiceAmount = filteredInvoicesList.reduce((acc, item) => acc + item.remainingAmount, 0);
    totalAmount = invoiceAmount - parseFloat(paymentData.discount);
  }

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Typography variant="h4"> Payments </Typography>


      <div style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
        <Button startIcon={<AddIcon />} color='primary' variant='contained' onClick={handleOpen}>
          ADD NEW PAYMENT
        </Button>
      </div>

      <Box sx={{ width: '100%' }}>
        <Tabs
          value={value}
          onChange={handleChange}
          textColor="secondary"
          indicatorColor="secondary"
          aria-label="secondary tabs example"
        >
          <Tab value="one" label="Vendors" />
          <Tab value="two" label="Expense/Income" />
        </Tabs>
      </Box>


      <div style={{ display: 'flex', justifyContent: 'flex-start', alignItems: 'center', marginTop: '10px', gap: "10px", padding: '10px', backgroundColor: '#E6F2FF', borderRadius: '10px' }}>


        <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
            <img src={CashImage} alt='currency_Image' />
            <Typography variant="subtitle1" color='#E11900'>CASH</Typography>
          </div>
          <div  >
            <Typography sx={{ fontSize: 24 }}>
              {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'CASH').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'CASH').totalAmount) : formatObj.format(0)}
            </Typography>

          </div>
        </div>

        {
          type[0] === 'VENDOR' ?
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
                <img src={CheckBook} alt='upi_Image' />
                <Typography variant="subtitle1" color='#008D2F'>CHEQUE</Typography>
              </div>
              <div  >
                <Typography sx={{ fontSize: 24 }}>
                  {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'CHEQUE').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'CHEQUE').totalAmount) : formatObj.format(0)}
                </Typography>
              </div>
            </div>
            :
            <>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
                  <img src={UPIImage} alt='upi_Image' />
                  <Typography variant="subtitle1" color='#008D2F'>UPI</Typography>
                </div>
                <div  >
                  <Typography sx={{ fontSize: 24 }}>
                    {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'UPI').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'UPI').totalAmount) : formatObj.format(0)}
                  </Typography>
                </div>
              </div>

              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'space-between', width: '200px', borderRadius: '8px', height: "100px", backgroundColor: 'white', padding: '15px' }}>
                <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-start', gap: '10px', alignItems: 'center' }}>
                  <img src={CreditCardImage} alt='card_Image' />
                  <Typography variant="subtitle1" color='#E11900'>CARD</Typography>
                </div>
                <div  >
                  <Typography sx={{ fontSize: 24 }}>
                    {data.typeSummaryDifferedByPaymentModeTypes.filter((mode) => mode._id === 'CARD').length > 0 ? formatObj.format(data.typeSummaryDifferedByPaymentModeTypes.find((mode) => mode._id === 'CARD').totalAmount) : formatObj.format(0)}
                  </Typography>
                </div>
              </div>
            </>

        }
      </div>



      {
        type[0] === 'VENDOR' ?
          null
          :
          <Stack direction="row" spacing={1} sx={{ mt: 2 }}>
            <Chip
              label={`Net Balance : ${Object.keys(data.balanceSummary).length > 0 ? formatObj.format(data.balanceSummary.totalAmount) : formatObj.format(0)}`}
              variant="outlined"
            />
            <Chip
              label={`Received Balance : ${Object.keys(data.balanceSummary).length > 0 ? formatObj.format(data.balanceSummary.receivedBalance) : formatObj.format(0)}`}
              variant="outlined"
            />
            <Chip
              label={`Paid Balance : ${Object.keys(data.balanceSummary).length > 0 ? formatObj.format(data.balanceSummary.paidBalance) : formatObj.format(0)}`}
              variant="outlined"
            />
          </Stack>
      }

      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginTop: '10px' }}>
        <TextField
          size="small"
          onChange={(e) => setSearchQuery(e.target.value)}
          type="text"
          placeholder="Search"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <SearchIcon />
              </InputAdornment>
            ),
          }}
        />
      </div>
      <div style={{ marginTop: "20px" }}>
        <DataTable
          columns={columns}
          data={filteredRecords}
          pagination
          progressPending={isLoading}
        />
      </div>

      <Modal
        open={open}
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2" sx={{ marginBottom: '10px' }}>
            Add Payment
          </Typography>


          <div style={{ backgroundColor: 'white', padding: '10px', borderRadius: '8px' }}>

            <Grid container spacing={2} >
              <Grid item xs={6}>
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">Party Type</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={paymentData.partyType}
                    label="Party Type"
                    onChange={(e) => setPaymentData((prev) => ({ ...prev, partyType: e.target.value }))}
                  >
                    <MenuItem value='VENDOR'>VENDOR</MenuItem>
                    <MenuItem value='EXPENSE'>EXPENSE</MenuItem>
                  </Select>
                </FormControl>
              </Grid>

              {
                paymentData.partyType === 'VENDOR'
                  ?
                  <Grid item xs={6}>
                    <Autocomplete
                      id="combo-box-demo"
                      options={vendors}
                      getOptionLabel={(option) => option.name}
                      onChange={(e, newValue) => {
                        setPaymentData((prev) => ({ ...prev, vendorId: newValue ? newValue._id : '' }));
                        if (newValue) {
                          setFilteredInvoicesList(invoicesList.filter((invoice) => invoice.vendorId._id === newValue._id));
                        }
                      }}
                      renderInput={(params) => <TextField {...params} label="Search Vendors" variant="outlined" />}
                      fullWidth
                    />
                  </Grid>
                  :
                  <Grid item xs={6}>
                    <FormControl fullWidth>
                      <InputLabel id="demo-simple-select-label">Select Category</InputLabel>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        name='category'
                        value={paymentData.category}
                        label="Select Category"
                        onChange={(e) => {
                          setPaymentData((prev) => ({ ...prev, category: e.target.value }))
                          if (e.target.value) {
                            invoicesList.forEach((item) => {
                              console.log(item.category, e.target.value);
                            })
                            setFilteredInvoicesList(invoicesList.filter((invoice) => invoice.category === e.target.value));
                          }
                        }}
                      >
                        {
                          categories.map((category, i) => (
                            <MenuItem key={i} value={category}>{category}</MenuItem>
                          ))
                        }
                      </Select>
                    </FormControl>
                  </Grid>
              }


              <Grid item xs={12}>
                <FormControl fullWidth>
                  <InputLabel id="demo-multiple-checkbox-label">Select Invoices</InputLabel>
                  <Select
                    disabled={!paymentData.vendorId && !paymentData.category}
                    labelId="demo-multiple-checkbox-label"
                    id="demo-multiple-checkbox"
                    name='invoices'
                    multiple
                    value={paymentData.invoices}
                    onChange={handleChangeSelect}
                    input={<OutlinedInput label="Select Invoices" />}
                    renderValue={(selected) => selected.join(', ')}
                    MenuProps={MenuProps}
                    fullWidth
                  >
                    {filteredInvoicesList.length > 0 && (
                      <MenuItem value="select-all" onClick={handleSelectAll}>
                        <Checkbox checked={paymentData.invoices.length === filteredInvoicesList.length} />
                        <ListItemText primary={paymentData.invoices.length === filteredInvoicesList.length ? 'Deselect All' : 'Select All'} />
                      </MenuItem>
                    )}
                    {filteredInvoicesList.map((invoiceObj, index) => (
                      <MenuItem key={index} value={invoiceObj.invoiceNumber}>
                        <Checkbox checked={paymentData.invoices.indexOf(invoiceObj.invoiceNumber) > -1} />
                        <ListItemText primary={invoiceObj.invoiceNumber} />
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              <Grid item xs={6}>
                <TextField
                  fullWidth
                  label="Invoice Amount"
                  type="number"
                  value={invoiceAmount}
                  disabled
                  name='invoiceAmount'
                />

              </Grid>
              <Grid item xs={6}>
                <TextField
                  name='discount'
                  fullWidth
                  value={paymentData.discount}
                  onChange={(e) => setPaymentData((prev) => ({ ...prev, discount: e.target.value }))}
                  onBlur={
                    () => {
                      if (parseFloat(paymentData.discount) > invoiceAmount) {
                        setPaymentData((prev) => ({ ...prev, discount: invoiceAmount }));
                        enqueueSnackbar('Discount cannot be greater than invoice amount!', { variant: 'error' });
                      };

                      if (paymentData.discount === '') {
                        setPaymentData((prev) => ({ ...prev, discount: 0 }));
                      }
                    }
                  }
                  label="Discount if any"
                  type="number"
                />

              </Grid>
              <Grid item xs={6}>
                <TextField
                  name='totalAmount'
                  fullWidth
                  disabled
                  value={totalAmount}
                  label="Total Amount"
                  type="number"
                />

              </Grid>
              <Grid item xs={6}>
                <LocalizationProvider dateAdapter={AdapterDayjs}>
                  <DatePicker
                    label="Payment Date"
                    value={paymentData.date}
                    format='DD/MM/YYYY'
                    maxDate={dayjs(new Date())}
                    onChange={(newValue) => {
                      setPaymentData((prev) => ({ ...prev, date: newValue }))
                    }}
                    slotProps={{ textField: { fullWidth: true } }}
                  />
                </LocalizationProvider>
              </Grid>

              <Grid item xs={12}>
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">Payment Mode</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={paymentData.paymentMode}
                    label="Payment Mode"
                    onChange={(e) => setPaymentData((prev) => ({ ...prev, paymentMode: e.target.value }))}
                  >
                    {
                      common.paymentMode.map((paymentMode) =>
                        <MenuItem value={paymentMode}>{paymentMode}</MenuItem>
                      )
                    }
                  </Select>
                </FormControl>
              </Grid>


              {
                paymentData.paymentMode !== 'CASH' &&
                <>
                  <Grid item xs={6}>
                    <FormControl fullWidth>
                      <InputLabel id="demo-simple-select-label">Select Bank</InputLabel>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={paymentData.bank}
                        onChange={(e) => setPaymentData((prev) => ({ ...prev, bank: e.target.value }))}
                        label="Select Bank"
                      >
                        {
                          banks.map(bank =>
                            <MenuItem key={bank._id} value={bank._id}>{bank.bankName}</MenuItem>
                          )
                        }
                      </Select>
                    </FormControl>
                  </Grid>

                  {
                    paymentData.paymentMode === 'CHEQUE' &&
                    <Grid item xs={6}>
                      <TextField
                        fullWidth
                        label="Cheque Number"
                        value={paymentData.chequeNumber}
                        onChange={(e) => setPaymentData((prev) => ({ ...prev, chequeNumber: e.target.value }))}
                        type="text"
                        name='chequeNumber'
                      />
                    </Grid>
                  }
                </>
              }
            </Grid>
          </div>


          <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '10px', marginTop: '20px' }}>
            <Button onClick={() => {
              setOpen(false);
              setPaymentData({
                partyType: 'VENDOR',
                vendorId: '',
                category: '',
                invoices: [],
                invoiceAmount: 0,
                discount: 0,
                totalAmount: 0,
                paymentDate: new Date(),
                paymentMode: '',
                chequeNumber: '',
                bank: ''
              })
            }} variant='outlined'>
              CANCEL
            </Button>
            <Button color='primary' variant='contained'
              disabled={
                paymentData.invoices.length === 0 ||
                !paymentData.paymentMode ||
                (paymentData.paymentMode !== 'CASH' && !paymentData.bank) ||
                (paymentData.paymentMode === 'CHEQUE' && !paymentData.chequeNumber)
              }
              onClick={handleAddPayment}
            >
              ADD
            </Button>
          </div>
        </Box>
      </Modal>

    </Container>
  );
}
